.payment {
    background-color: #fff8f8;
}

.payment-container > h1 {
    text-align: center;
    padding: 10px;
    font-weight: 500;
    background-color: #95B8D1;
    border-bottom: 1px solid #95B8D1;
}

.payment-container > h1 a {
    text-decoration: none;
}

.payment-section {
    display: flex;
    padding: 20px;
    margin: 0 20px;
    border-bottom: 1px solid #95B8D1;
}

.payment-title {
    flex: 0.2;
}

.payment-address, .payment-items, .payment-details {
    flex: 0.8;
}

.payment-details > form {
    max-width: 400px;
  }

.payment-details > h3 {
    padding-bottom: 20px;
}


.payment-details > form > div > button {
    background: #cfd6bc;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    font-weight: bolder;
    margin-top: 10px;
    border-color: #cfd6bc;
    color: #111;
}

