.checkoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}

.checkoutProduct_info {
    padding-left: 20px;
}

.checkoutProduct_info > button {
    background: #cfd6bc;
    border-radius: 2px;
    height: 40px;
    border: 1px solid #cfd6bc;
    margin-top: 10px;
    width:fit-content;
    
}

.checkoutProduct_image {
    object-fit: contain;
    width: 180px;
    height: 180px
}


.checkoutProduct_title {
    font-size: 17px;
    font-weight:700;
}